import { IconButton } from "@mui/material";
import { Swiper, SwiperSlide } from "swiper/react/swiper-react";
import { Navigation } from "swiper";

import hnhImg from "../assets/hnh.png";
import HondaImg from "../assets/honda-c.svg";

const Testimonial = ({ className }) => {
  return (
    <section className={className}>
      <div className="content">
        <h4>{"Relax! You are in good hands."}</h4>
        <div className="slider-container">
          <Swiper
            modules={[Navigation]}
            spaceBetween={0}
            slidesPerView={1}
            loop={true}
            navigation={{
              prevEl: `.${className} .btn-swiper.prev`,
              nextEl: `.${className} .btn-swiper.next`,
            }}
            breakpoints={{
              585: {
                slidesPerView: 1,
                spaceBetween: 0,
              },
              768: {
                slidesPerView: 1,
                spaceBetween: 0,
              },
            }}
          >
            <SwiperSlide>
              <div className="slide-card">
                <img src={HondaImg} alt="honda" className="slide-image" />
                <div className="info">
                  <p className="quote">
                    {
                      "“EasySocial has helped us manage our enquiries in real time. Substantial amount of time has been saved in deliberation of enquiries to our dealerships, earlier we had to manually call, access and then map enquiries to each dealership, now the process is automated. We are extremely satisfied with the commitment & innovation shown by team EasySocial and we are happy to recommend their services to other businesses.”"
                    }
                  </p>
                  <div className="author">
                    <p>{"Mohit Sharma"}</p>
                    <p>{"Executive, Corporate & Brand"}</p>
                    <p>{"Honda Motorcycle & Scooter India Pvt. Ltd."}</p>
                  </div>
                </div>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="slide-card">
                <img src={hnhImg} alt="hnh" className="slide-image" />
                <div className="info">
                  <p className="quote">
                    {
                      "“We've seen our online sales increase by 60%. It has proved to be a boon in our business. The interface of easysocial is easy to use and  we are completely satisfied with the product. We highly recommend it to all businesses a stepping stone towards conversational commerce.”"
                    }
                  </p>
                  <div className="author">
                    <p>{"Akshay Kochhar"}</p>
                    <p>{"Founder & CEO"}</p>
                    <p>{"Haven & Hearth Homestays India"}</p>
                  </div>
                </div>
              </div>
            </SwiperSlide>
          </Swiper>
          <div className={"slider-nav"}>
            <IconButton className={"btn-swiper prev"} aria-label={"Slide Left"}>
              <svg width="36" height="21" viewBox="0 0 36 21" fill="none">
                <path
                  d="M11.5 2L3 10.5L11.5 19"
                  stroke="#12B0EE"
                  strokeWidth="3"
                />
                <path
                  d="M3.39536 10.5H35.4186"
                  stroke="#12B0EE"
                  strokeWidth="3"
                />
              </svg>
            </IconButton>
            <IconButton
              className={"btn-swiper next"}
              aria-label={"Slide Right"}
            >
              <svg width="36" height="21" viewBox="0 0 36 21" fill="none">
                <path
                  d="M24.5 2L33 10.5L24.5 19"
                  stroke="#12B0EE"
                  strokeWidth="3"
                />
                <path
                  d="M32.6047 10.5H0.581397"
                  stroke="#12B0EE"
                  strokeWidth="3"
                />
              </svg>
            </IconButton>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Testimonial;
